.Background__Container {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: absolute;
  top: 0;
  z-index: -9999;
}

.Background__Fade {
  position: relative;
  width: 100%;
  min-height: 60vh;
  top: -25px;
  z-index: 1;
}

.Background__Scroll {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  position: relative;
  top: -20px;
  color: #feda4a;
  font-family: equinox;
  font-size: 500%;
  font-weight: 600;
  letter-spacing: 10px;
  line-height: 150%;
  height: 800px;
  -webkit-perspective: 400px;
          perspective: 400px;
  text-align: justify;
}

.Background__Crawl {
  position: relative;
  top: 9999px;
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-animation: crawl 35s linear;
          animation: crawl 35s linear;
}

.Background__Crawl > .Background__Title {
  font-size: 90%;
  text-align: center;
}

.Background__Crawl > .Background__Title h1 {
  margin: 0 0 100px;
  text-transform: uppercase;
}

@-webkit-keyframes crawl {
  0% {
    top: 0;
    -webkit-transform: rotateX(20deg) translateZ(0);
            transform: rotateX(20deg) translateZ(0);
  }
  100% {
    top: -7000px;
    -webkit-transform: rotateX(25deg) translateZ(-3000px);
            transform: rotateX(25deg) translateZ(-3000px);
  }
}

@keyframes crawl {
  0% {
    top: 0;
    -webkit-transform: rotateX(20deg) translateZ(0);
            transform: rotateX(20deg) translateZ(0);
  }
  100% {
    top: -7000px;
    -webkit-transform: rotateX(25deg) translateZ(-3000px);
            transform: rotateX(25deg) translateZ(-3000px);
  }
}



